.login{
    width: 100vw;
    height: 100vh;
    background-color: #f0f2f5;
    display: flex;
    align-items: center;
    justify-content: center;
}
.loginWrapper{
    width: 70%;
    height: 70%;
    display: flex;
}
.loginLeft,
.loginRight{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.loginLogo {
    font-size: 50px;
    font-weight: 800;
    color: #1775ee;
    margin-bottom: 10px;
  }
  .loginDesc {
    font-size: 24px;
  }
  .loginBox{
    height: 400px;
    padding: 20px;
    background-color: #fff;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .loginInput{
    height: 40px;
    border-radius: 10px;
    border: 1px solid grey;
    font-size: 18px;
    padding-left: 20px;
    outline: none;
    margin-top: 20px;
    padding: 10px;
  }
  .loginButton{
    height: 70px;
    border: none;
    border-radius: 10px;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    color: #fff;
    background-color: #1775ee;
    margin-top: 20px;
  }
  .loginForgot{
    text-align: center;
    color: #1775ee;
  }
  .loginRegisterButton{
    width: 60%;
    align-self: center;
    height: 70px;
    border-radius: 10px;
    border: none;
    background-color: #42b72a;
    color: white;
    font-size: 18px;
    font-weight: 500;
    cursor: pointer;
    margin-top: 20px;
}
  